<template>
  <!-- 合并式 -->
  <div>
    <CellGroup inset class="cell-group" style="margin-top: 10px">
      <Collapse v-model="activeNames">
        <CollapseItem name="1" is-link>
          <template #title>
            <div>{{ title }}: <span style="color: #1989fa">WegtSn</span></div>
          </template>

          <!-- 图片显示 -->
          <Grid :column-num="3" v-if="imageShow == true">
            <GridItem>
              <div class="active">
                <VanImage
                  src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg"
                />
                <span>WegtSn</span>
              </div>
            </GridItem>
          </Grid>
          <!-- 型号设置界面 -->
          <CellGroup inset class="cell-group">
            <Cell
              :title="item.title"
              :is-link="index > 1"
              :value="item.value"
              @click="showPickerFn(item)"
              v-for="(item, index) in titleColumns"
              :key="index"
            />
          </CellGroup>
        </CollapseItem>
      </Collapse>
    </CellGroup>
  </div>

  <Popup round v-model:show="showPicker" position="bottom">
    <AlarmPicker
      :columns="columns"
      @confirm="onConfirm"
      :defaultIndex="defaultIndex"
      @cancel="showPicker = false"
    />
  </Popup>
</template>


<script setup>
import {
  CellGroup,
  Cell,
  Popup,
  CollapseItem,
  Collapse,
  Grid,
  GridItem,
  Image as VanImage,
} from "vant"; // Checkbox
import AlarmPicker from "@/components/Alarm//AlarmPicker.vue";
import {
  defineComponent,
  ref,
  defineProps,
  watchEffect,
  watch,
  defineEmits,
  reactive,
} from "vue";
const porps = defineProps(["cmdCmdInfo", "loadCmdInfo"]);
import { columnComInfo } from "@/utlis/QueryStr";
const emit = defineEmits(["comConfirm"]);
const activeNames = ref(["1"]);
const columnIndex = columnComInfo();
const defultVal = "请选择";

const titleColumns = reactive([
  {
    title: "型号",
    value: defultVal,
    tipsIndex: 2,
  },
  {
    title: "串口号",
    value: defultVal,
    tipsIndex: 1,
  },
  {
    title: "波特率",
    value: defultVal,
    tipsIndex: 3,
  },
  {
    title: "数据位",
    value: defultVal,
    tipsIndex: 4,
  },
  {
    title: "停止位",
    value: defultVal,
    tipsIndex: 5,
  },
  {
    title: "校验位",
    value: defultVal,
    tipsIndex: 6,
  },
]);

const imageShow = ref(porps.imageShow ?? true); // 是否显示图片选择项
const title = ref("型号"); // 标题
const defaultIndex = ref(0);

// const cmdCmdInfo = ref(porps.cmdCmdInfo ?? []);

const columns = ref([]);
const columnsCom = ["1", "2", "3", "4"]; // 波特率
const columnsFunction = ref([]); // 功能
const columnsBps = [
  "115200",
  "57600",
  "38400",
  "19200",
  "9600",
  "4800",
  "2400",
  "1200",
  "300",
]; // 波特率
const columnsDatabits = ["8", "7", "6"]; // 波特率
const columnsStopbits = ["1", "2", "3"]; // 停止位
const columnsCheckbits = ["无校验", "奇校验", "偶校验"]; // 停止位
// const columnsCheckbitsIndex = ["78", "79", "69"]; // 停止位
const columnOilType = ["TUB", "N08", "PLUG", "ITALON"];
const showPicker = ref(false);
const pockerIndex = ref(0);
// const data = ref([]);

watchEffect(
  async () => {
    // cmdCmdInfo.value = porps.cmdCmdInfo;
    console.log(porps.cmdCmdInfo);
    // data.value = porps.data;
    titleColumns.forEach((item, index) => {
      if (index == 0) {
        item.value = columnIndex[porps.cmdCmdInfo[index]];
      } else {
        item.value = porps.cmdCmdInfo[index];
      }
    });
  },
  { flush: "post" }
);

watch(porps, async () => {
  getData();
});

const getData = () => {
  var items = [];
  emit("comConfirm", items);
};

// 显示选择器
const showPickerFn = (item) => {
  var num = item.tipsIndex;
  var value = item.value;
  pockerIndex.value = num;
  console.log(num);
  if(num == 1 || num == 2) {
    return;
  }
  switch (num) {
    case 1:
      columns.value = columnsCom;
      break;
    case 2:
      columns.value = columnsFunction.value;
      break;
    case 3:
      columns.value = columnsBps;
      break;
    case 4:
      columns.value = columnsDatabits;
      break;
    case 5:
      columns.value = columnsStopbits;
      break;
    case 6:
      columns.value = columnsCheckbits;
      break;
    case 7:
      columns.value = columnOilType;
      break;
  }
  console.log(columns.value);
  defaultIndex.value = columns.value.indexOf(value);
  showPicker.value = true;
};

// picker 点击  确认
const onConfirm = (value) => {
  showPicker.value = false;
  let pocker = pockerIndex.value;
  titleColumns.forEach((item) => {
    if (item.tipsIndex == pocker) {
      item.value = value[0];
    }
  });
};

// 命名空间
defineComponent({
  name: "yunweibao-ComConfig",
});
</script>

<style scoped>
.active {
  border: 1px solid #1989fa;
  border-radius: 5px;
  overflow: hidden;
  padding: 5px;
  color: #1989fa;
}
.defultStyle {
  border: 1px solid #eff2f5;
  border-radius: 5px;
  overflow: hidden;
  padding: 5px;
}
.cell-info:first-child {
  margin-top: 10px;
}
.cell-group {
  margin-bottom: 10px;
}
</style>