<template>
  <TabHeaders :navTitle="navTitle" :leftArrow="false" />
  <ComConfig :loadCmdInfo="loadCmdInfo" :cmdCmdInfo="cmdCmdInfo" />

  <CellGroup inset class="cell-group">
    <Field
      type="tel"
      label="当前电压值"
      label-width="120"
      placeholder="请输入"
      input-align="right"
      v-model="loadCmdInfo[2]"
    >
      <template #button>
        <span>MV</span>
      </template></Field
    >
    <Field
      type="tel"
      label="当前重量"
      label-width="120"
      placeholder="请输入"
      input-align="right"
      v-model="loadCmdInfo[3]"
    >
      <template #button>
        <span>KG</span>
      </template>
    </Field>
  </CellGroup>

  <!-- 标定数目 -->
  <div style="margin-top: 20px; background: #ffffff">
    <Row>
      <Col span="16">
        <CellGroup>
          <Field
            type="tel"
            label="标定数目"
            label-width="120"
            placeholder="请输入标定数目"
            input-align="right"
            v-model="loadCmdInfo[4]"
          />
        </CellGroup>
      </Col>
      <Col span="8">
        <Button
          type="danger"
          size="small"
          style="margin-right: 10px; margin-top: 6px; float: right"
          >删除</Button
        >
        <Button
          type="primary"
          size="small"
          style="float: right; margin-top: 6px; margin-right: 10px"
          >添加</Button
        >
      </Col>
    </Row>
  </div>

  <Row>
    <Col offset="1" span="22">
      <CellGroup
        style="margin-top: 10px"
        v-for="(item, index) in 2"
        :key="index"
      >
        <Cell>
          <template #title>
            <Checkbox shape="square">序号 {{ item }}</Checkbox>
          </template>
        </Cell>
        <Field
          type="tel"
          label="当前重量"
          label-width="120"
          placeholder="请输入"
          input-align="right"
        >
          <template #button>
            <span>KG</span>
          </template>
        </Field>
        <Field
          type="tel"
          label="标定重量"
          label-width="120"
          placeholder="请输入"
          input-align="right"
        >
          <template #button>
            <span>KG</span>
          </template>
        </Field>
      </CellGroup>
    </Col>
  </Row>
  <footer style="padding-bottom: 50px"></footer>
  <StickyBottom />
</template>


<script setup>
/*
报警器 和 刷卡器 共用
*/
import TabHeaders from "@/components/tab.vue";
import StickyBottom from "@/components/stickyBottom.vue";
import ComConfig from "@/components/ComConfig.vue";
import { postAN } from "@/utlis/AdApi";
import {
  Col,
  Row,
  Cell,
  Field,
  CellGroup,
  Button,
  Checkbox,
  Toast,
} from "vant"; // Checkbox
import { defineComponent, ref, onMounted } from "vue";
import { useI18n } from "vue-i18n";
const { t } = useI18n();

const navTitle = ref("载重");
const resultAll = ref("");
const maxNum = ref(0);
const comAllCmdInfo = ref([]);
const cmdCmdInfo = ref([]);
const loadCmdInfo = ref([]);
// 命名空间
defineComponent({
  name: "yunweibao-LoadSettings",
});


// 解析串口数据
const getComInfo = (comCmd) => {
  var comArr = [];
  comCmd.forEach((item) => {
    if (item.indexOf("52") !== -1) {
      var its = item.split("~");
      its[1] = its[0];
      its[0] = "52";
      cmdCmdInfo.value = its;
    }
    var it = item.split("~");
    it[1] = it[1].split("*")[0];
    comArr.push(it);
  });
  return comArr;
};

// -------------------------------------------------------------------
// 安卓回调函数r
const callJSResult = (str) => {
  var cmds = str.split(";")[0];
  maxNum.value++;
  resultAll.value += cmds + "!";
  if (maxNum.value == 3) {
    var allcmd = resultAll.value.split("!");
    var comCmd = allcmd[0].split(",").splice(1); // 串口指令
    var loadCmd = allcmd[1].split(",").splice(1); 
    console.log("--------------------------------")
    loadCmdInfo.value = loadCmd;
    console.log(loadCmd)
    allcmd.pop(); // 去除最后一位的空值
    comAllCmdInfo.value = getComInfo(comCmd);
    return;
  }
};
// 安卓成功失败回调
const callJSResult_Status = (str) => {
  if (str.indexOf("Success") !== -1) {
    Toast.success(t("toast[1]"));
  } else {
    Toast.fail(t("toast[2]"));
  }
};

// 向安卓发送指令
const androidStatus_fn = () => {
  postAN.ANSend("$UARTSET");
  setTimeout(() => {
    postAN.ANSend("$PREWEIGHT");
  }, 500);
  setTimeout(() => {
    postAN.ANSend("$WEGIHTCAL");
  }, 1000);
};
androidStatus_fn();

onMounted(() => {
  window.callJSResult = callJSResult;
  window.callJSResult_Status = callJSResult_Status;
});
</script>

<style scoped>
.cell-group {
  margin-top: 10px;
}
.checkbox {
  margin-top: 0px;
  margin-bottom: 15px;
}
.trInfo {
  width: 30vh;
  text-align: center;
  height: 30px;
}
</style>